  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  height: 820px;
  background-color: #2b1c6f;
  background-image: url(img/map/net.svg);
  background-size: 200%;
  background-position: center;
  z-index: 10;
  margin-top: -20px;
  overflow: hidden;
  position: relative;
}
@media (max-width: 992px) and (min-width: 740px) {
