}
.objects object {
  width: 1940px;
  margin-top: 50px;
  height: 860px;
  z-index: 10;
}
.objects__mask {
  z-index: 999;
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
}
.objects__map-wrapper {
  position: relative;
  transform: scale(0.85);
  top: -50px;
  z-index: 100;
}
@media (max-width: 1025px) and (min-width: 992px) {
  .objects__map-wrapper {
    transform: scale(0.75);
  }
}
@media (max-width: 992px) and (min-width: 740px) {
  .objects__map-wrapper {
    transform: scale(0.55);
  }
}
@media (max-width: 740px) and (min-width: 320px) {
  .objects__map-wrapper {
    transform: scale(0.3);
  }
}
.objects .container {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
}
.objects .container--top {
  top: 0;
}
.objects .container--bottom {
  bottom: 0;
}
.objects__box {
  position: absolute;
  left: 0;
  top: 60px;
  z-index: 99999;
}
@media (max-width: 992px) and (min-width: 740px) {
  .objects__box {
    left: 0;
  }
}
@media (max-width: 740px) and (min-width: 320px) {
  .objects__box {
    left: 0;
    top: 20px;
  }
}
.objects__box h3 {
  color: #fff;
  font-size: 48px;
  font-weight: 900;
}
@media (max-width: 992px) and (min-width: 740px) {
  .objects__box h3 {
    font-size: 40px;
  }
}
@media (max-width: 740px) and (min-width: 320px) {
  .objects__box h3 {
    font-size: 24px;
  }
}
.objects__nav {
  border-radius: 10px;
  background-color: rgba(118, 74, 255, 0.5);
  position: absolute;
  bottom: 40px;
  right: 0;
  padding: 13px;
  z-index: 99999;
}
@media (max-width: 992px) and (min-width: 740px) {
  .objects__nav {
    right: 0;
    padding: 0 15px;
  }
}
@media (max-width: 740px) and (min-width: 320px) {
  .objects__nav {
    right: 0;
    padding: 0 15px;
    bottom: 20px;
  }
}
.objects__btn {
  display: block;
  font-size: 16px;
  color: #fff;
  text-decoration: none;
  line-height: 50px;
  padding-right: 50px;
  position: relative;
  transition: 0.3s;
  cursor: pointer;
}
.objects__btn:after {
  content: '';
  display: inline-block;
  width: 33px;
  height: 33px;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background-repeat: no-repeat;
  background-size: contain;
}
.objects__btn--plus:after {
  background-image: url('img/plus_icon.png');
}
.objects__btn--minus:after {
  background-image: url('img/minus_icon.png');
}
.objects__btn:hover {
  opacity: 0.6;
  transition: 0.3s;
}
.objects__text {
  font-size: 14px;
  padding: 30px 10px;
  border-radius: 10px;
  border-bottom-left-radius: 0;
  position: absolute;
  background-color: rgba(255, 255, 255, 0.8);
  color: #261861;
  width: 400px;
  bottom: 100%;
  left: 20px;
  display: none;
  cursor: default;
}
.objects__close {
  width: 35px;
  height: 35px;
  background-image: url('img/close_icon.png');
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 2px;
  right: 2px;
  z-index: 999;
  cursor: pointer;
}
.objects__marker {
  position: absolute;
  top: 50%;
  left: 50%;
  text-decoration: none;
  width: 35px;
  height: 45px;
  z-index: 999;
  cursor: pointer;
  background-image: url('img/plus_icon.png');
}
.objects__marker:hover {
  z-index: 9999;
}
